
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Search from 'vux/src/components/search/index.vue'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
XInput,
XButton,
Popup,
Search
},
data() {
return {
phoneNum: '',
showAddress: false,
addressCity: '中国',
addressNum: '0086',
items: [
{
id: 1,
title: '中国',
value: '0086'
}, {
id: 2,
title: '日本',
value: '0082'
}, {
id: 3,
title: '韩国',
value: '0023'
}, {
id: 4,
title: '美国',
value: '0032'
}
],
isNext: true
}
},
methods: {
isRegister() {
if (this.phoneNum == '15622633852') {
this.nextRegister()
}
},
nextRegister() {
let password_sha = hex_sha1(hex_sha1(this.phoneNum));

let loginParam = {
phoneNum: this.phoneNum,

password_sha
}
this.$http.post('http://localhost:9090/data', {
params: {
id: 1,
loginParam
},
}).then((res) => {
console.log(res)
if (res.status === 201) {
setTimeout(() => {



this.$router.push('/register/SMS')
}, 1000)
} else {
this.show = true
}
}), (err) => {
console.log(err)
}
},
clickAddress() {
this.showAddress = true
},
clickNum(item) {
this.addressNum = item.value
this.addressCity = item.title
this.showAddress = false
},
clickNext() {
if (this.phoneNum.length === 11) {
this.$vux.loading.show()
setTimeout(() => {
this.$vux.loading.hide()
this.$router.push('/SMS_password')
}, 500)
} else {
this.$vux.alert.show({
content: '手机号码错误'
})
}
}
},
watch: {
phoneNum: function (val, oldVal) {
if (this.phoneNum !== '') {
this.isNext = false
} else {
this.isNext = true
}
}
}
}



