
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
XHeader,
XButton,
XInput,
Group
},
data() {
return {
code: '',
password: '',
showGet: true,
isGet: false,
timer: null,
count: '',
isOk: true,
isPwd: 'password',
isShow: true
}
},
created() {
this.clickGetCode()
},
methods: {
isNews() {
if (this.password !== '') {
this.regSuccess()
}
},
regSuccess() {
let password_sha = hex_sha1(hex_sha1(this.password));

let loginParam = {
password: this.password,
password_sha
}
this.$http.post('http://localhost:9090/data', {
params: {
loginParam
},
id: 1,
}).then((res) => {
console.log(res)
if (res.status === 201) {
setTimeout(() => {



this.$router.push('/login')
}, 1000)
}
}), (err) => {
console.log(err)
}
},
clickGetCode() {
const TIME_COUNT = 60
if (!this.timer) {
this.count = TIME_COUNT
this.showGet = false
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--
} else {
this.showGet = true
clearInterval(this.timer)
this.timer = null
}
}, 1000)
}
},
clickOk() {
this.$vux.loading.show()
setTimeout(() => {
this.$vux.loading.hide()
this.$router.push('/login')
}, 500)
},
clickShow() {
this.isShow = !this.isShow
if (this.isShow == false) {
this.isPwd = 'text'
} else {
this.isPwd = 'password'
}
}
},
watch: {
code: function () {
if (this.code !== '' && this.password !== '') {
this.isOk = false
} else {
this.isOk = true
}
},
password: function () {
if (this.code !== '' && this.password !== '') {
this.isOk = false
} else {
this.isOk = true
}
}
}
}



