
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
data() {
return {
showRoot: false,
menus: {
menu1: '邀请加入',
menu2: '开发加入',
menu3: '锁定'
},
key: 'menu3'
}
},
methods: {
clickSave() {
this.$vux.loading.show({
text: '加载中'
})
setTimeout(() => {
var _this = this
this.$vux.loading.hide(() => {
})
_this.$router.push('/me/my_family')
}, 1000)
},
clickRoot() {
this.showRoot = !this.showRoot
},
click (key) {
if (key !== 'cancel') {
this.key = key
}
},
},
components: {
Actionsheet,
XHeader,
Qrcode,
Group,
Cell,
XButton,
XInput,
Flexbox,
FlexboxItem
}
}



