
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
data() {
return {
list: [
{
id: 1,
name: "开",
code: "K01",
switchStatus: true
},
{
id: 2,
name: "关",
code: "K02",
switchStatus: false
},
{
id: 3,
name: "开",
code: "K01",
switchStatus: false
},
{
id: 4,
name: "关",
code: "K02",
switchStatus: false
}
],
switchStatus: false
}
},
created() {

},
methods: {
clickMore() {
console.log('123')
},
clickCode(e) {
this.$vux.loading.show({
text: 'loading'
})

this.$http.get('http://localhost/controlCode?code=' + e.target.innerText)
.then((res) => {
console.log(res)
})
.catch((err) => {
console.log(err)
})

setTimeout(() => {
this.$vux.loading.hide()
}, 3000)

}
},
components: {
XHeader,
XTable,
XButton,
XSwitch,
LoadMore
}
}



