
import Social from '../common/SocialAccount.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
data() {
return {
showLogin: false,
showErr: false,
phone: '',
password: '',
isLogin: true,
isPwd: 'password',
isShow: true,
isNewsLogin: true,
showGet: true,
code: '',
isCode: true,
username: ''
}
},
methods: {
login(){
if (this.phone !== '' && this.password !== '') {
this.toLogin();
this.isLogin = false
} else {
this.showErr = true
}
},
toLogin(){
var _this = this
this.$http.post('http://localhost:8080/SmartHome/login', {
phone: _this.phone,
password: _this.password
})
.then((res) => {
console.log(res)
this.$vux.loading.show()
localStorage.setItem('mydata', JSON.stringify(res.body[0]))
var mydata = JSON.parse(localStorage.mydata)
this.$store.commit('updateUserInfo', mydata)
if (res.status === 200 && res.bodyText !== 'loginFail') {
setTimeout(() => {
let expireDays = 1000 * 60 * 60 * 24 * 15;
this.setCookie('session', res.body, expireDays);
this.$vux.loading.hide()
this.$router.push('/Home')
}, 2000)
} else {
this.$vux.loading.hide()
this.showErr = true
this.phone = ''
this.password = ''
}
})
.catch((err) => {
this.$vux.loading.show()
setTimeout(() => {
this.$vux.loading.hide()
this.$vux.alert.show({
content: '网络故障'
})
this.phone = ''
this.password = ''
}, 5000)
})
},
clickThird() {
this.$vux.alert.show({
content: '亲，后续待完善！'
})
},
clickShow() {
this.isShow = !this.isShow
if (this.isShow == false) {
this.isPwd = 'text'
} else {
this.isPwd = 'password'
}
},
clickNewsLogin() {
this.showLogin = !this.showLogin
},
clickForgetPwd() {
this.$router.push('/forget_password')
},
clickClose() {
this.showLogin = !this.showLogin
this.phone = ''
this.code = ''
},
clickGetCode() {
const TIME_COUNT = 60
if (!this.timer) {
this.count = TIME_COUNT
this.showGet = false
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--
} else {
this.showGet = true
clearInterval(this.timer)
this.timer = null
}
}, 1000)
}
},
clickLogin() {
this.$vux.loading.show()
setTimeout(() => {
this.$vux.loading.hide()
this.phone = ''
this.code = ''
this.$router.push('/home')
}, 1000)
}
},
watch: {
phone: function (val, oldVal) {
if (this.phone !== '' && this.password !== '') {
this.isLogin = false
} else {
this.isLogin = true
}
},
password: function (val, oldVal) {
if (this.phone !== '' && this.password !== '') {
this.isLogin = false
} else {
this.isLogin = true
}
},
phone: function () {
if (this.phone !== '') {
this.isCode = false
} else {
this.isCode = true
}
}
},
components: {
XHeader,
Alert,
Group,
XInput,
XButton,
Social,
Popup
}
}



