
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
data() {
return {
showMore: false,
userId: '',
familyId: '',
family: '',
familyName: '',
createdName: ''
}
},
created() {
var that = this
var mydata = JSON.parse(localStorage.mydata)
this.userId = mydata.user.id
this.$http.get('http://localhost:8080/SmartHome/user/' + this.userId, {})
.then((res) => {
localStorage.setItem('newData', JSON.stringify(res.body[0]))
var newData = JSON.parse(localStorage.newData)
this.$http.get('http://localhost:8080/SmartHome/family/' + newData.familyId, {})
.then((res) => {
this.family = res.body[0].family
})
console.log(newData)
}), (err) => {
console.log(err)
}
},
methods: {
clickMore() {
this.showMore = !this.showMore
},
createFamily() {
this.$router.push('/me/create_family')
},
joinFamily() {
this.$router.push('/me/join_family')
}
},
components: {
XHeader,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton,
}
}



