
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
data() {
return {
createdName: '我的家庭',
familyCode: '例：xxx',
familyId: ''
}
},
created() {
var newData = JSON.parse(localStorage.newData)
this.familyId = newData.familyId
},
methods: {
saveCreated() {
this.$http.post('http://localhost:8080/SmartHome/family/'+this.familyId, {
familyName: this.createdName,
familyUniqueCode: this.familyCode
})
.then((res)=>{
if (res.status === 200 && res.bodyText !== '') {
localStorage.setItem('family', JSON.stringify(res.body[0]))
var family = JSON.parse(localStorage.family)
console.log(family)
this.$store.commit('createdManage', this.createdName)
this.$vux.loading.show({})
setTimeout(() => {
this.$vux.loading.hide()
this.$router.push('/me/my_family')
}, 1000)
}else {
console.log('输入错误')
}
})
.catch((res)=>{console.log(res)})
}
},
components: {
XHeader,
XInput,
XButton,
Group
}
}



