
import Loading from 'vux/src/components/loading/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import Panel from "../../../node_modules/vux/src/components/panel/index";
export default {
data () {
return {
userInfo: [],
show2: false,
nickname: '',
phone: '',
family: '',
menus2: {
menu1: '拍照',
menu2: '从手机相册选择'
},
isLogouting: false,
}
},
created() {
var mydata = JSON.parse(localStorage.mydata)
this.userInfo = mydata.user
console.log(mydata)
this.nickname = mydata.user.username
this.phone = mydata.user.phone

},
methods: {
showPhoto() {
this.show2 = !this.show2
localStorage.removeItem('wifiIconStatus')
localStorage.removeItem('completeStatus')
},
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$vux.loading.hide()
}, 3000)
},
logout(){

this.showLoading()
this.delCookie('session');

setTimeout(() => {
this.$router.push('/login/');
this.isLogouting = false;
}, 3000)

this.$http.get('http://120.79.21.193/SmartHome/logout')
.then((res) => {
console.log(res.body)
})
.catch((err) => {
console.log(err)
})
},
clickDemo() {
this.$vux.alert.show({
content: '后续待完善！'
})
setTimeout(() => {
this.$vux.alert.hide()
}, 1000)
}
},
components: {
Loading,
Panel,
Actionsheet,
XSwitch,
XButton,
XImg,
Cell,
Group,
XHeader
}
}



